* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #757474;
  display: inline-block;
}


img {
  max-width: 100%;
  line-height: 0;
  display: block;
}

p {
  margin: 10px 0;
}

header {
  text-align: center;
  padding: 5px;
  background-color: #1d70be;
}

header a {
  display: flex;
  align-items: flex-end;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.thanks {
  text-align: center;
  font-size: 16px;
  font-style: italic;
  margin-right: 10px;
  padding: 0;
  color: white;
}

header>.container {
  position: relative;
}

body {
  font-family: 'Varela Round', sans-serif;
  font-size: 10px;
  font-weight: 400;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.2;
  background-repeat: no-repeat;
  margin: 0;
}




label {
  position: relative;
  display: inline-flex;
  transform: translateX(13px);
  height: 30px;
}

.gallery-box {
  padding: 20px 0 40px 0; 
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  text-align: center;
}

.gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
  text-align: center;
  margin-bottom: 40px;
}

.photo-card {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
  transition: all 250ms ease-in-out;
}

.photo-card:hover >img{
  transform: scale(1.02);
  filter: drop-shadow(8px 8px 5px rgba(71, 71, 71, 0.418));
  transition: all 250ms ease-in-out;
}

.photo-card > img {
  transition: all 250ms ease-in-out;
  width: 100%;
  height: 174px;
  object-fit: cover;
}

.info {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 0;
  width: 100%;
}



.info-item{
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.info-item:hover >span{
  transition: transform .3s ease-in-out;
  transform: translateY(-250%);
}

.info-item:hover>p{
  transition: transform .3s ease-in-out;
  transform: translateY(-200%);
}

.info-item>p {
position: relative;
transition: transform .3s ease-in-out;
margin: 10px 0;
}

.info-item>span {
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 100%;
  margin: 10px 0;
}



.search-form__input {
  margin-right: 0;
  border: none;
  font-size: 0.7rem;
  transition: width 250ms;
  padding-top: 5px;
  padding-left: 5px;
  padding-bottom: 5px;
  width: 110px;
  height: 100%;
  color: #757474;
  border-radius: 10px;
  outline: none;
}

.search-form__button {
  margin-left: 0;
  border: none;
  font-size: 1.15em;
  background: none;
  color: #aaa;
  padding-top: 2px;
  padding-bottom: 2px;
  transition: color 200ms;
  cursor: pointer;
  transform: translateX(-100%);
  transition: transform 250ms;
}

.search-form__button>svg{  
  fill: #757474;
}

label:hover .search-form__input,
label:focus .search-form__input {
  width: 160px;
}
label:hover .search-form__button,
label:focus .search-form__button{
  transition: transform 250ms;
  transform: translateX(0);
}
label:hover .search-form__button>svg,
label:focus .search-form__button>svg{
  transition: fill 250ms;
  fill: #fff;
}

.load-more {
  padding: 5px 40px;
  height: 30px;
  background-color: #1d70be;
  color: #fff;  
  border-radius: 10px;
  border: none;
  cursor: pointer;
}

.is-hidden {
  display: none;
}
